  onSearch() {

    let empRes = [];

    this.showCard = this.rbtns === '1' ? true : false;


    this.onClear();

    console.log('Search Param', this.searchParam);
    console.log('NgBtns', this.rbtns);
    if (this.searchParam === '') {
      this.found = true;
      this.onClear();
      return;
    }
    if (this.rbtns === '1') {
       this.employees = this.employees.filter(employee => (employee.name.trim().toUpperCase() === this.searchParam.trim().toUpperCase()));
       empRes = this.employees.map(emp => {this.employee = emp; });
   } else if (this.rbtns === '2') {
      this.employees = this.employees.filter(employee => (employee.company.trim().toUpperCase() === this.searchParam.trim().toUpperCase()));
        empRes = this.employees.map(emp => { this.employee = emp; });
      }


    if (empRes.length === 0){
      this.found = false;
     // this.onClear();
    } else {
      this.found = true;
    }

  }

  onSelectfromList(firstName) {
      console.log('Clicked', firstName );
      this.searchParam = firstName;
      this.rbtns = '1';
      this.onSearch();
  }


  onClear() {
    this.employees = this.oldData;
  }
